@use './config/variables.scss' as v;

.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.header {
  grid-column: 1 / 3;
  margin: 0 0 10px 0;
  font-size: 24px;
  color: v.$midBrown;
  text-align: center;
}

