@use '../../config/variables.scss' as v;

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  padding: 12px;
  background-color: rgba(v.$lightBrown, 40%);
  box-shadow: 0px 1px 3px 0px v.$midBrown;
  border: 2px solid v.$brown;
  border-radius: 5px;
}