@use '../../config/variables' as v;
@use '../../config/mixins';

.wrapper {
  display: flex;
  align-items: center;
  column-gap: 5px
}

.text {
  color: v.$brown;
  font-size: 18px;
  font-weight: bold;
}

.input {
  @include mixins.input;
  width: 55px;
  border-radius: 6px;
}

.changeValueButton {
  @include mixins.button;
  height: 30px;
  width: 30px;
  margin-left: 5px;
}